import React, { Fragment, useState, useEffect } from "react";
import MaterialTable from "material-table";
import BotonCambiarPagina from "../components/botonCambiarPagina";
// import io from "socket.io-client";
import axios from "axios";
export default function() {
  const [state, setState] = useState({
    columns: [
      {
        title: "Número",
        field: "numero",
        type: "numeric"
      },
      {
        title: "Tela",
        field: "tipo_tela",
        lookup: {
          algodon: "Algodon",
          otro: "otro"
        }
      },
      {
        title: "Modelo",
        field: "modelo",
        lookup: {
          ramera: "Ramera",
          otro: "otro"
        }
      },
      { title: "hojas", field: "hojas", type: "numeric" },
      { title: "talles", field: "talles", type: "numeric" },
      { title: "Total", field: "total", type: "numeric" },

      { title: "Salida estampado", field: "estampado_salida", type: "date" },
      { title: "Entrada estampado", field: "estampado_entrada", type: "date" },
      {
        title: "Aplique",
        field: "aplique",
        lookup: {
          estampado: "Estampado",
          bordado: "Bordado"
        }
      },
      {
        title: "Despachado al taller",
        field: "despachado",
        lookup: {
          gary: "Gary",
          mario: "Mario",
          gabriela: "Gabriela",
          ronal: "Ronal"
        }
      },
      {
        title: "Salida taller",
        field: "taller_salida",
        type: "date"
      },
      { title: "Entrada taller", field: "taller_entrada", type: "date" },
      { title: "Salida lavadero", field: "lavadero_salida", type: "date" },
      { title: "Entrada lavadero", field: "lavadero_entrada", type: "date" }
    ],
    data: [],
    socket: "",
    messages: [],
    pagina: {
      pagina: 0,
      _id: "sinide"
    }
  });

  useEffect(() => {
    // state.socket = io("/");
    // state.socket.on("message", message => {
    //   console.log(state);
    //   setState({ ...state, message: [...state.messages, message] });
    // });
    getPagina();
  }, []);

  // function enviar(event) {
  //   const body = "hola como estas";
  //   const message = {
  //     body,
  //     from: "Me"
  //   };
  //   setState({ ...state, messages: [...state.messages, message] });
  //   state.socket.emit("message", body);
  //   event.target.value = "";
  // }

  const getPagina = () => {
    axios
      .post("/getPagina", {})
      .then(response => {
        console.log(response);
        let datosPaginacion = {
          pagina: {
            pagina: response.data[0].pagina,
            _id: response.data[0]._id
          },
          numero_corte: {
            numero: response.data[1].numero,
            _id: response.data[1]._id
          }
        };

        getRow(datosPaginacion);
      })
      .catch(error => {
        console.log(error);
        console.log("error");
      });
  };

  const anterior = () => {
    let pagina =
      state.pagina.pagina === 1
        ? { ...state.pagina, pagina: 1 }
        : { ...state.pagina, pagina: state.pagina.pagina - 1 };

    setPagina(pagina);
  };

  const siguiente = () => {
    let pagina = { ...state.pagina, pagina: state.pagina.pagina + 1 };

    setPagina(pagina);
  };

  const setPagina = pagina => {
    changeTable(pagina.pagina);
    axios
      .put("/pagina", {
        ...pagina
      })
      .then(function(response) {})
      .catch(function(error) {
        console.log(error);
      });
  };

  const formartDate = newDate => {
    newDate = new Date(newDate);
    let day = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    let fecha = `${month}.${day}.${year}`;
    return fecha;
  };

  const numeroActual = () => {
    let numero = state.numero_corte.numero.find(element => {
      return element.pagina === state.pagina.pagina;
    });
    if (numero === undefined) {
      let newNumber = state.numero_corte.numero;
      newNumber.push({
        numero: 1,
        pagina: state.pagina.pagina
      });
      setState({
        ...state,
        numero_corte: {
          ...state.numero_corte,
          numero: newNumber
        }
      });

      axios
        .put("/pagina", {
          ...state.numero_corte,
          numero: newNumber
        })
        .then(function(response) {})
        .catch(function(error) {
          console.log(error);
        });
      return 1;
    } else {
      if (numero.numero === 50) {
        let nuevoArreglo = state.numero_corte.numero.map(element => {
          if (element.pagina === state.pagina.pagina) {
            return { numero: 1, pagina: element.pagina };
          } else return element;
        });
        setState({
          ...state,
          numero_corte: {
            ...state.numero_corte,
            numero: nuevoArreglo
          }
        });
        axios
          .put("/pagina", {
            ...state.numero_corte,
            numero: nuevoArreglo
          })
          .then(function(response) {})
          .catch(function(error) {
            console.log(error);
          });
        return 1;
      } else {
        let nuevoArreglo = state.numero_corte.numero.map(element => {
          if (element.pagina === state.pagina.pagina) {
            return { numero: element.numero + 1, pagina: element.pagina };
          } else return element;
        });
        setState({
          ...state,
          numero_corte: {
            ...state.numero_corte,
            numero: nuevoArreglo
          }
        });
        axios
          .put("/pagina", {
            ...state.numero_corte,
            numero: nuevoArreglo
          })
          .then(function(response) {})
          .catch(function(error) {
            console.log(error);
          });
        return numero.numero + 1;
      }
    }
  };
  const addRow = newRow => {
    let addNewRow = {
      ...newRow,
      numero: numeroActual(),
      total:
        newRow.hojas !== undefined && newRow.talles !== undefined
          ? parseInt(newRow.hojas) * parseInt(newRow.talles)
          : 0,
      estampado_salida:
        newRow.estampado_salida !== undefined
          ? formartDate(newRow.estampado_salida)
          : undefined,
      estampado_entrada:
        newRow.estampado_entrada !== undefined
          ? formartDate(newRow.estampado_entrada)
          : undefined,
      taller_salida:
        newRow.taller_salida !== undefined
          ? formartDate(newRow.taller_salida)
          : undefined,
      taller_entrada:
        newRow.taller_entrada !== undefined
          ? formartDate(newRow.taller_entrada)
          : undefined,
      lavadero_salida:
        newRow.lavadero_salida !== undefined
          ? formartDate(newRow.lavadero_salida)
          : undefined,
      lavadero_entrada:
        newRow.lavadero_entrada !== undefined
          ? formartDate(newRow.lavadero_entrada)
          : undefined
    };

    const data = [...state.data];
    data.push(addNewRow);
    setState({ ...state, data });
    axios
      .post("/row", {
        ...addNewRow,
        pagina: state.pagina.pagina
      })
      .then(function(response) {
        console.log("hola");
      })
      .catch(function(error) {
        console.log(error);
      });
  };
  const getRow = datosPaginacion => {
    axios
      .post("/getRow", datosPaginacion.pagina)
      .then(response => {
        console.log(response);
        setState({
          ...state,
          data: [...state.data, ...response.data],
          ...datosPaginacion
        });
      })
      .catch(error => {
        console.log("error");
      });
  };

  const changeTable = pagina => {
    axios
      .post(`/getRow`, { pagina: pagina })
      .then(response => {
        console.log(response);
        setState({
          ...state,
          data: [...response.data],
          pagina: { ...state.pagina, pagina: pagina }
        });
        console.log("cambio");
      })
      .catch(error => {
        console.log("error");
      });
  };

  const deleteRow = oldData => {
    axios
      .delete(`/row/${oldData._id}`, {
        oldData
      })
      .then(response => {
        const data = [...state.data];
        data.splice(data.indexOf(oldData), 1);
        setState({ ...state, data });
      })
      .catch(error => {
        console.log(error);
      });
  };
  const updateRow = (newRow, oldData) => {
    let newData = {
      ...newRow,
      total:
        newRow.hojas !== undefined && newRow.talles !== undefined
          ? parseInt(newRow.hojas) * parseInt(newRow.talles)
          : 0,
      estampado_salida:
        newRow.estampado_salida !== undefined
          ? formartDate(newRow.estampado_salida)
          : undefined,
      estampado_entrada:
        newRow.estampado_entrada !== undefined
          ? formartDate(newRow.estampado_entrada)
          : undefined,
      taller_salida:
        newRow.taller_salida !== undefined
          ? formartDate(newRow.taller_salida)
          : undefined,
      taller_entrada:
        newRow.taller_entrada !== undefined
          ? formartDate(newRow.taller_entrada)
          : undefined,
      lavadero_salida:
        newRow.lavadero_salida !== undefined
          ? formartDate(newRow.lavadero_salida)
          : undefined,
      lavadero_entrada:
        newRow.lavadero_entrada !== undefined
          ? formartDate(newRow.lavadero_entrada)
          : undefined
    };

    const data = [...state.data];
    data[data.indexOf(oldData)] = newData;
    setState({ ...state, data });
    axios
      .put("/row", newData)
      .then(response => {
        console.log("holaaa");
      })
      .catch(error => {
        alert(error);
      });
  };
  return (
    <Fragment>
      <BotonCambiarPagina
        siguiente={siguiente}
        pagina={state.pagina}
        anterior={anterior}
      />
      <MaterialTable
        title="CONTROL DE PRODUCIÓN"
        columns={state.columns}
        data={state.data}
        editable={{
          onRowAdd: newData =>
            new Promise(resolve => {
              setTimeout(() => {
                resolve();
                addRow(newData);
              }, 100);
            }),
          onRowUpdate: (newData, oldData) =>
            new Promise(resolve => {
              setTimeout(() => {
                resolve();
                updateRow(newData, oldData);
              }, 100);
            }),
          onRowDelete: oldData =>
            new Promise(resolve => {
              setTimeout(() => {
                resolve();
                deleteRow(oldData);
              }, 100);
            })
        }}
      />
    </Fragment>
  );
}
